.sectionHeaderWrapper{display:flex;align-items:center;justify-content:center;width:100%;min-height:clamp(25rem,60vh,50rem);padding:0;position:relative}@media(max-width: 768px){.sectionHeaderWrapper{flex-direction:column;min-height:auto;padding:2rem 1rem;gap:1rem}}.image{position:absolute;top:50%;left:0;transform:translateY(-50%);height:100%;width:auto;object-fit:contain;z-index:1;pointer-events:none}@media(max-width: 768px){.image{position:relative;top:auto;transform:none;width:100%;height:auto;z-index:0;margin-top:0}}.textContainer{position:relative;z-index:2;display:flex;align-items:center;justify-content:center;width:100%;text-align:center}@media(max-width: 768px){.textContainer{position:static}}.sectionHeaderText{font-family:var(--font-cormorant);font-size:6rem;font-weight:600;line-height:1.1;color:#f0c332;margin:0;max-width:50rem}@media(max-width: 768px){.sectionHeaderText{font-size:4rem}}